.footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    background-color: #222;
    display: flex;
    justify-content: space-around;
    padding: 10px 0;
  }
  
  .menu-button, .cart-button {
    background-color: #f4a444;
    color: #000;
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  





  .footer {
    background-color: #222; /* Фон подвала */
    color: #f4a444; /* Цвет текста */
    text-align: center;
    padding: 10px 0;
    box-shadow: 0px -2px 5px rgba(0, 0, 0, 0.5); /* Лёгкая тень сверху */
    font-size: 14px;
    width: 100%;
}




/* Основная структура страницы */
body {
  display: flex;
  flex-direction: column;
  min-height: 100vh; /* Гарантирует, что тело страницы занимает всю высоту окна */
  margin: 0; /* Убираем лишние отступы */
}

.main-content {
  flex: 1; /* Расширяет основной контент, чтобы занять всё доступное пространство */
  padding-bottom: 10px; /* Добавляем отступ, чтобы оставить место под подвал */
}
/* Основное содержимое страницы */
.main-content {
  padding-bottom: 60px; /* Добавляем отступ снизу, равный высоте подвала */
}

/* Подвал */
.footer {
  position: fixed; /* Закрепляем подвал */
  bottom: 0; /* Располагаем подвал внизу */
  width: 100%; /* Растягиваем подвал по всей ширине */
  background-color: #222; /* Цвет фона */
  color: #f4a444; /* Цвет текста */
  text-align: center;
  padding: 10px 0; /* Внутренний отступ */
  box-shadow: 0px -2px 5px rgba(0, 0, 0, 0.5); /* Лёгкая тень сверху */
  font-size: 14px;
  z-index: 100;
}
